@font-face {
  font-family: Arial;
  src: url(../../fonts/Arial.woff2), url(../../fonts/Arial.woff);
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: Arial;
  src: url(../../fonts/Arial-Italic.woff2), url(../../fonts/Arial-Italic.woff);
  font-style: italic;
  font-weight: 400;
}
@font-face {
  font-family: Georgia;
  src: url(../../fonts/Georgia.woff2), url(../../fonts/Georgia.woff);
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: Georgia;
  src: url(../../fonts/Georgia-Bold.woff2), url(../../fonts/Georgia-Bold.woff);
  font-style: normal;
  font-weight: 700;
}
@font-face {
  font-family: Georgia;
  src: url(../../fonts/Georgia-Italic.woff2), url(../../fonts/Georgia-Italic.woff);
  font-style: italic;
  font-weight: 400;
}
*,
*::before,
*::after {
  margin: 0;
  border: 0;
  padding: 0;
  box-sizing: border-box;
}

ol,
ul,
li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

html, body {
  height: 100%;
}

body {
  font-family: Georgia, serif;
  font-weight: 400;
  font-size: 1rem;
  letter-spacing: normal;
  line-height: normal;
  font-style: normal;
  background: #FFFFFF;
  color: #545454;
}

._container {
  margin: auto;
  overflow: hidden;
  max-width: 1200px;
  padding: 0 40px;
  box-sizing: content-box;
}

h2 {
  font-family: Georgia, serif;
  font-weight: 400;
  font-size: 2.75rem;
  letter-spacing: normal;
  line-height: 130%;
  font-style: normal;
}

h3 {
  font-family: Georgia, serif;
  font-weight: 400;
  font-size: 2.1875rem;
  letter-spacing: 2.1px;
  line-height: 130%;
  font-style: normal;
}

.h3-mobile {
  font-family: Georgia, serif;
  font-weight: 400;
  font-size: 1.5625rem;
  letter-spacing: 1.5px;
  line-height: 130%;
  font-style: normal;
}

h4 {
  font-family: Georgia, serif;
  font-weight: 400;
  font-size: 1.25rem;
  letter-spacing: 1.2px;
  line-height: 115%;
  font-style: normal;
}

h5 {
  font-family: Georgia, serif;
  font-weight: 400;
  font-size: 0.9375rem;
  letter-spacing: 0.9px;
  line-height: 110%;
  font-style: normal;
}

.h5-modal-window {
  font-family: Georgia, serif;
  font-weight: 700;
  font-size: 0.9375rem;
  letter-spacing: 0.9px;
  line-height: 110%;
  font-style: normal;
}

.paragraph-l {
  font-family: Arial, sans-serif;
  font-weight: 400;
  font-size: 0.9375rem;
  letter-spacing: normal;
  line-height: 160%;
  font-style: normal;
}

.paragraph-s {
  font-family: Arial, sans-serif;
  font-weight: 400;
  font-size: 0.75rem;
  letter-spacing: normal;
  line-height: 150%;
  font-style: italic;
}

.paragraph-mobile-menu {
  font-family: Arial, sans-serif;
  font-weight: 400;
  font-size: 2rem;
  letter-spacing: normal;
  line-height: 160%;
  font-style: normal;
}

.logo__title {
  font-family: Georgia, serif;
  font-weight: 400;
  font-size: 2rem;
  letter-spacing: 1.92px;
  line-height: 110%;
  font-style: normal;
}

.logo__subtitle {
  font-family: Arial, sans-serif;
  font-weight: 400;
  font-size: 0.8125rem;
  letter-spacing: 1.3px;
  line-height: normal;
  font-style: normal;
}

.header {
  position: relative;
}

.header_absolute {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 20;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #444444;
  padding-top: 30px;
}

.logo {
  display: flex;
  flex-direction: column;
}

.logo > *:first-child {
  margin: 0 0 10px 0;
}

.logo__title {
  display: block;
  color: #F1CDB3;
}

.logo__subtitle {
  color: #FFFFFF;
  text-align: right;
}

.nav-menu {
  display: flex;
}

.nav-menu > *:not(:last-child) {
  margin: 0 35px 0 0;
}

.nav-menu__link {
  color: #CDCDCD;
}

.nav-menu__link_active {
  color: #FAFAFA;
  padding: 0 0 7px 0;
  border-bottom: 3px solid #F1CDB3;
}

.not-only {
  position: relative;
}

.not-only__container {
  padding-top: 180px;
  display: flex;
  background-color: #444444;
}

.not-only__body {
  padding: 163px 0 243px 0;
  max-width: 460px;
}

.not-only__body > *:not(:last-child) {
  margin: 0 0 42px 0;
}

.not-only__title {
  color: #FFFFFF;
}

.not-only__subtitle {
  color: #CDCDCD;
}

.not-only__make-friend {
  display: flex;
  justify-content: flex-start;
}

.not-only__btn-make-friend {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #292929;
  padding: 15px 45px;
  border-radius: 100px;
  background-color: #F1CDB3;
}

.about__container {
  padding-top: 80px;
  padding-bottom: 100px;
  display: flex;
}

.about__container > * {
  flex: 0 1 50%;
}

.about__image {
  display: flex;
  align-items: center;
  justify-content: center;
}

.about__body {
  padding: 14px 0;
  max-width: 380px;
}

.about__body > *:not(:last-child) {
  margin: 0 0 25px 0;
}

.about__title {
  color: #545454;
}

.about__subtitle {
  color: #4C4C4C;
}

.pets__container {
  padding-top: 80px;
  padding-bottom: 100px;
}

.pets__container > *:not(:last-child) {
  margin: 0 0 60px 0;
}

.pets__title {
  text-align: center;
}

.pets-slider {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 105px;
}

.pets-slider__item {
  background-color: #FAFAFA;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 435px;
}

.pets-slider__item > * {
  margin: 0 0 30px 0;
}

.pet-slider__pet-name {
  color: #545454;
}

.pet-slider__btn-learn-more {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #292929;
  padding: 15px 45px;
  border-radius: 100px;
  border: 2px solid #F1CDB3;
}

.pets__rest {
  display: flex;
  align-items: center;
  justify-content: center;
}

.pets__btn-rest {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #292929;
  padding: 15px 45px;
  border-radius: 100px;
  background-color: #F1CDB3;
}

.help__container {
  padding-top: 80px;
  padding-bottom: 100px;
}

.help__title {
  text-align: center;
  margin: 0 0 60px 0;
}

.help__body {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  column-gap: 120px;
  row-gap: 55px;
}

.help__item {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.help-item__img {
  width: 60px;
  margin: 0 0 30px 0;
}

.help-item__title {
  text-align: center;
}

.donation-container {
  padding-top: 80px;
  padding-bottom: 100px;
}

.donation__row {
  padding: 0 142px 0 143px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.donation__body {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0 0 0 30px;
}

.donation__body > *:not(:last-child) {
  margin: 0 0 20px 0;
}

.donation__title {
  color: #545454;
}

.bank-name {
  color: #545454;
}

.credit-card {
  display: flex;
  background-color: #F1CDB3;
  padding: 10px 15px;
  border-radius: 9px;
}

.credit-card__number {
  padding: 0 0 0 15px;
}

.donation__info {
  color: #B2B2B2;
}

.footer-container {
  padding-top: 40px;
  background-color: #545454;
  display: flex;
}

.contacts__title ~ *, .location__title ~ * {
  color: #F1CDB3;
}

.contacts > *:not(:last-child) {
  margin: 0 0 40px 0;
}

.contacts__title, .location__title {
  color: #FFFFFF;
}

.contacts__email, .contacts__phone, .location__item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.contacts__email-name, .contacts__phone-number, .location__name {
  padding: 0 0 0 20px;
}

.location__title {
  margin: 0 0 34px 0;
}

.location__item:not(:last-child) {
  margin: 0 0 32px 0;
}